<template>
  <div class="manageaddres">
    <div class="manageaddresbox">
      <div class="manageaddres-top">
        <van-nav-bar title="地址管理" left-text="返回" left-arrow @click-left="onClickLeft" />
      </div>
      <div class="manageaddress-body">
        <van-address-list
          v-model="chosenAddressId"
          :list="addresslist"
          default-tag-text="默认"
          @add="gonewaddres"
          @edit="editaddress"
        />
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/Manageaddress.less";
import areaList from "../assets/js/arealist";
import { utils } from "../assets/js/utils";
export default {
  created() {
    this.getaddresslist();
  },
  data() {
    return {
      areaList,
      searchResult: [],
      chosenAddressId: "1",
      addresslist: [],
    };
  },
  methods: {
    //返回上一级
    onClickLeft() {
      this.$router.go(-1);
    },
    //新增地址
    gonewaddres() {
      this.$router.push({ name: "Newaddress" });
    },
    // 获取地址
    getaddresslist() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 20000) {
            result.data.result.map((v) => {
              let address = v.province + v.city + v.county + v.addressDetail;
              this.addresslist.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address,
                isDefault: Boolean(v.isDefault),
              });
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 编辑地址
    editaddress(item, index) {
      this.$router.push({
        name: "Newaddress",
        query: { aid: item.id },
      });
    },
  },
};
</script>

<style lang="less" scoped>
</style>